import React from 'react'
import jwtDecode from 'jwt-decode'
import Provider from 'react-auth-guard'
import Loading from './Loading'
import NotAuthenticated from './NotAuthenticated'
import Authenticated from './Authenticated'
import { isPropertySignature } from 'typescript'

const authGetters = {
    getUser: () => ({
        firstName: "ბადრი",
        lastName: "123",
    }),
}



const fetchUser = ({ token }) => new Promise((resolve, reject) => {
    const { sub } = jwtDecode(token)
    if (sub.toString() === '1') {
        return resolve()
    }
    return reject()
})

function Inputs(props) {
    return (
        <div>
            <label>სახელი:</label>
            <input type="text" value={props.firstName} onInput={props.setInputFirstName} />
            <label>გვარი:</label>
            <input type="text" value={props.lastName} onInput={props.setInputLastName} />
            <br />
        </div>
    )
}

function Login() {
    const [inputName, setInputFirstName] = React.useState('');
    const [inputLastName, setInputLastName] = React.useState('');

    return (

        <div>
            <Provider
                fetchUser={fetchUser}
                getters={authGetters}
                onLogout={() => alert('გამოთესლდი')}
                onLoginFail={() => alert('ვერ შეთესლდი')}
                // onLogin={({ userId }) => alert(`შეთესლდი ამ აიდით == ${userId}!`)}
            >
                {({ authenticating, authenticated }) => (
                    <Loading isLoading={authenticating}>
                        {
                            authenticated
                                ? null
                                : <Inputs value={inputName} onInput={e => setInputFirstName(e.target.value)}/>
                        }
                        {
                            authenticated
                                ? <Authenticated />
                                : <NotAuthenticated />
                        }
                    </Loading>
                )}
            </Provider>

        </div>
    )
}

export default Login;