import React from 'react'
import { withAuth } from 'react-auth-guard'


const getFullname = ({ firstName, lastName }) => [firstName, lastName].join(' ')

const Authenticated = ({ auth, firstName, lastName }) => (
  <div>
    <h1>შეთესლებული ხარ</h1>
    <h3>{getFullname(auth.getUser())}</h3>
    {/* <h2>{firstName + " " + lastName}</h2> */}
    <h5><button type="button" onClick={auth.logout}>გამოთესლება</button></h5>
  </div>
)

export default withAuth(Authenticated)